<template>
	<view class="container1">
		<view class="container2">
			<tag class="tag"></tag>
		</view>
		<view class="container3">
			<speech class="message" @getIcon="getIcon()" :iconList="iconList"></speech>
		</view>
	</view>
</template>

<script>
	import tag from '../../components/tag/tag.vue'
	import speech from '../../components/speech/speech.vue'
	export default {
		components: {
			tag,
			speech
		},
		data() {
			return {
				iconList: []
			}
		},
		onLoad() {
			this.getIcon()
		},
		methods: {
			getIcon() {
				uni.request({
					url: 'https://api-hmugo-web.itheima.net/api/public/v1/home/swiperdata', //仅为示例，并非真实接口地址。
					success: (res) => {
						this.iconList = res.data.message;
						console.log(this.iconList);
					},
					fail: () => {
						console.log("失败了")
					}
				});
			}
		}
	}
</script>

<style scoped lang="scss">
	.container1 {
		height: 100vh;
		overflow: hidden;
		background-color: #eee;

		.container2 {
			height: 200rpx;
			margin: 20rpx 20rpx;
			background-color: white;
			border-radius: 20rpx;
		}

		.container3 {
			height: auto;
			margin: 20rpx 20rpx;
			background-color: white;
			border-radius: 20rpx;

			.message {
				padding: 20rpx 30rpx;
			}
		}

	}
</style>
